<template>
  <div class="admin-registration-step-3">
    <RegistrationTitle>
      {{ t('authentication.twoFactor') }}
    </RegistrationTitle>
    <form class="d-flex flex-column align-items-center" @submit.prevent>
      <QrCode height="128" width="126" />
      <Text
        type="uppercase"
        size="16px"
        line-height="19px"
        opacity="0.4"
        mt="18px"
        mb="28px"
      >{{ code }}</Text>
      <Input
        v-model="form.code.value"
        hide-shadow
        bordered
        :placeholder="tp('enterCode')"
      />
      <Button type="primary" font-size="14px" @click="onSubmit">
        {{ t('common.continue') }}
      </Button>
    </form>
  </div>
</template>

<script>
import { reactive, ref } from 'vue'
import { useI18n } from '@/shared/plugins/i18n'
import Text from '@/shared/components/UIKit/Text/Text'
import QrCode from '@/shared/components/QrCode'
import RegistrationTitle from '@/admin/views/Registration/RegistrationTitle'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'

export default {
  name: 'Step3',
  components: {
    Button,
    Text,
    Input,
    QrCode,
    RegistrationTitle
  },
  emits: ['submit'],
  setup (_, { emit }) {
    const { tp, t } = useI18n('adminRegistration')

    const code = ref('nFdKSMphbPCh0oYL')

    const form = reactive({
      code: {
        value: ''
      }
    })

    const onSubmit = () => {
      emit('submit', form)
    }

    return {
      t,
      tp,
      code,
      form,
      onSubmit
    }
  }
}
</script>

<style lang="scss">
.admin-registration-step-3 {
  padding: 34px 55px 0 55px;

  .admin-registration-title {
    margin-bottom: 29px;
  }
  form {
    .input {
      width: 100%;
      margin-bottom: 20px;
      padding: 13px 20px;
    }
    .button {
      width: 100%;
    }
  }
}
</style>
